body {
  margin: 0;
  padding: 0;
}
.card {
  -webkit-transition: .3s;
  -o-transition: .3s;
  transition: .3s;
}
.card:hover {
  background-color: #ddd;
  cursor: pointer;
  border: 3px solid #ccc;
}
.container-button-loading {
  height: 100vh;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
}
.container-artist-detail {
  width: 50%;
  margin: 12px auto 12px auto;
  text-align: center;
}
@media(max-width: 800px) {
  .container-artist-detail {
    width: 100%;
  }
}



